import * as React from 'react';
import {
    ColumnDef,
    ColumnFiltersState,
    SortingState,
    VisibilityState,
    flexRender,
    getCoreRowModel,
    getFacetedRowModel,
    useReactTable,
} from '@tanstack/react-table';

import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';

import { DataTablePagination } from './data-table-pagination';
import { FetchDataOptions, Filters } from '@/types/table.types';
import { cn } from '@/lib/utils';

interface DataTableProps {
    columns: ColumnDef<any>[];
    data: any[];
    pageCount: number;
    totalCount: number;
    onFetchData: (options: FetchDataOptions) => void;
    isLoading: boolean;
    DataTableToolbar: any;
    cellPading?: boolean;
}

export function ShiftsDataTable({
    columns,
    data,
    pageCount,
    totalCount,
    onFetchData,
    isLoading,
    DataTableToolbar,
    cellPading = true,
}: DataTableProps) {
    const [rowSelection, setRowSelection] = React.useState({});
    const [columnVisibility, setColumnVisibility] = React.useState<VisibilityState>({});
    const [columnFilters, setColumnFilters] = React.useState<ColumnFiltersState>([]);
    const [sorting, setSorting] = React.useState<SortingState>([]);
    const [globalFilter, setGlobalFilter] = React.useState('');
    const [filters, setFilters] = React.useState<Filters>({});
    const [{ pageIndex, pageSize }, setPagination] = React.useState({
        pageIndex: 0,
        pageSize: 10,
    });

    const handleRefreshTable = () => {
        fetchDataRef.current(fetchOptions);
    };

    const enhancedData = data.map((row) => ({
        ...row,
        refreshTable: handleRefreshTable,
    }));

    // Track if this is the initial render
    const isInitialRender = React.useRef(true);

    // Create a stable reference to the fetch function
    const fetchDataRef = React.useRef(onFetchData);

    // Update the reference when onFetchData changes
    React.useEffect(() => {
        fetchDataRef.current = onFetchData;
    }, [onFetchData]);

    // Memoize the fetch options to prevent unnecessary re-renders
    const fetchOptions = React.useMemo(() => {
        return {
            pageIndex,
            pageSize,
            sorting,
            filters,
            globalFilter,
        };
    }, [pageIndex, pageSize, sorting, JSON.stringify(filters), globalFilter]);

    // Handle fetching data with proper dependency tracking
    React.useEffect(() => {
        // Skip the first render to prevent double-fetching
        if (isInitialRender.current) {
            isInitialRender.current = false;
            fetchDataRef.current(fetchOptions);
            return;
        }

        // Use a timeout to debounce frequent changes (especially for search/filter)
        const handler = setTimeout(() => {
            fetchDataRef.current(fetchOptions);
        }, 300);

        return () => {
            clearTimeout(handler);
        };
    }, [fetchOptions]);

    const table = useReactTable({
        data: enhancedData,
        columns,
        pageCount,
        state: {
            sorting,
            columnVisibility,
            rowSelection,
            columnFilters,
            pagination: {
                pageIndex,
                pageSize,
            },
        },
        manualPagination: true,
        manualSorting: true,
        manualFiltering: true,
        enableRowSelection: true,
        onRowSelectionChange: setRowSelection,
        onSortingChange: setSorting,
        onColumnVisibilityChange: setColumnVisibility,
        onColumnFiltersChange: setColumnFilters,
        onPaginationChange: setPagination,
        getCoreRowModel: getCoreRowModel(),
        getFacetedRowModel: getFacetedRowModel(),
    });

    return (
        <div>
            <DataTableToolbar
                table={table}
                globalFilter={globalFilter}
                setGlobalFilter={setGlobalFilter}
                filters={filters}
                setFilters={setFilters}
            />
            <div className="rounded-md">
                <Table>
                    <TableHeader>
                        {table.getHeaderGroups().map((headerGroup) => (
                            <TableRow key={headerGroup.id} className="border-none">
                                {headerGroup.headers.map((header) => {
                                    return (
                                        <TableHead key={header.id} className="font-bold px-0">
                                            {header.isPlaceholder
                                                ? null
                                                : flexRender(header.column.columnDef.header, header.getContext())}
                                        </TableHead>
                                    );
                                })}
                            </TableRow>
                        ))}
                    </TableHeader>
                    <TableBody>
                        {isLoading ? (
                            <TableRow>
                                <TableCell colSpan={columns.length} className="h-24 text-center">
                                    جاري التحميل...
                                </TableCell>
                            </TableRow>
                        ) : table.getRowModel().rows?.length ? (
                            table.getRowModel().rows.map((row) => (
                                <TableRow key={row.id} data-state={row.getIsSelected() && 'selected'}>
                                    {row.getVisibleCells().map((cell) => (
                                        <TableCell
                                            className={cn('', {
                                                'p-4': cellPading,
                                                'p-0': !cellPading,
                                            })}
                                            key={cell.id}
                                        >
                                            {flexRender(cell.column.columnDef.cell, cell.getContext())}
                                        </TableCell>
                                    ))}
                                </TableRow>
                            ))
                        ) : (
                            <TableRow>
                                <TableCell colSpan={columns.length} className="h-24 text-center">
                                    لا توجد بيانات متاحة
                                </TableCell>
                            </TableRow>
                        )}
                    </TableBody>
                </Table>
            </div>
            <DataTablePagination table={table} totalCount={totalCount} />
        </div>
    );
}
